<template>
  <div class="header">
    <div v-for="(item , index) in list" :key="index" class="content" @click="tag(item)">
      <img :src="item.pic" alt />
      <h3>{{item.name}}</h3>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      list: []
    };
  },
  created() {
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
      duration: 0
    });
    this.axios({
      method: "GET",
      url: "/byclass",
      params: {
        appkey: this.appkey,
        classid: 565,
        start: 0,
        num: 30
      }
    })
      .then(res => {
        this.$toast.clear();
        for (let i = 0; i < res.data.result.list.length; i++) {
          this.list.push(res.data.result.list[i]);
        }
      })
      .catch(err => {
        this.$toast.clear();
           
      });
  },
  methods: {
    ...mapMutations(["changeData"]),
    tag(item) {
      this.$router.push({ name: "detail" });
      this.changeData({ key: item.name, value: item });
      sessionStorage.setItem("detail", JSON.stringify(item));
    }
  }
};
</script>

<style lang="less" scoped>
.header{
  height: 660px;
  overflow: auto;
  .content {
  width: 90%;
  margin-top: 30px;
  text-align: center;
  margin: 30px auto;
  border-bottom: 1px solid #666;
  img {
    border-radius: 10px;
  }
  h3 {
    font-size: 20px;
    color: #666;
  }
}
}


</style>